.DashContainer {
  display: flex;
  height: 100vh;
  position: relative;
  font-size: 18px;
}

.SideBar {
  width: 15%; /* laki nung side box */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #152360; /* sidenav color */
  padding: 30px;
}

.NavList {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.NavLogo {
  margin-bottom: 20px;
  text-align: center;
}

.dashLink {
  display: block;
  padding: 10px 0;
  text-decoration: none;
  color: #ead994; 
  font-weight: bold;
  font-size: 25px;
}

.dashLink:hover {
  color: white; /* Adjust the hover color as needed */
}

.logoutBtn {
  margin-top: 70vh;
}

.BGsaKANAN {
  width: 50%;
  position: fixed;
  right: 0;
  top: 0;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f0f0f0;
}

.BGsaKANAN img {
  max-width: 100%;
  height: 100%;
}
